<script setup lang="ts">
    import useFetch from "@/composables/useFetch";
    import { ref, watch } from "vue";
    import { useField } from "vee-validate";
    import useGetCommonOps from "@/composables/useGetCommonOps";

    const emit = defineEmits<{
        fetchList: [p: CloseRangeResDto];
    }>();

    const clubKindCd = ref<"1" | "2" | "3">("1");

    const sidoArr = ref<SidoList[] | null>(null);
    const chosenSido = ref<SidoList | null>(null);

    const sigunguArr = ref<SigunguList[] | null>(null);
    const chosenSigungu = ref<SigunguList[]>([]);

    const grEvfxCd = ref<string>("");
    const clubNm = ref<string>("");

    const grEvfxCdOps = ref<OpsList[] | null>();

    const etcOps = ref<any>({
        pkgPsbYn: "N",
        wifiOffrYn: "N",
        shtlOprtYn: "N",
        dsbpFaclOffrYn: "N"
    });

    useGetCommonOps({
        code: "CO010",
        onSuccess: (data: CDList[]) => {
            grEvfxCdOps.value = [
                { label: "종목전체", value: "" },
                ...data.map((a: CDList) => ({ value: a.commnCd, label: a.cdNm }))
            ] as OpsList[];
        }
    });

    useFetch<SidoList[]>({
        url: "/search/kind-si-do",
        param: () => ({ clubKindCd: clubKindCd.value }),
        dependancy: [() => clubKindCd.value],
        onSuccess: data => {
            // data.shift();
            console.log(data);
            sidoArr.value = data;
            chosenSido.value = { ...data[0] };
        }
    });

    useFetch<SigunguList[]>({
        url: "/search/kind-si-gun-gu",
        dependancy: [() => chosenSido.value],
        enable: () => !!chosenSido.value,
        param: () => ({ siDoCd: chosenSido.value?.siDoCd, clubKindCd: clubKindCd.value }),
        onSuccess: data => {
            sigunguArr.value = data;
            chosenSigungu.value.push({ ...data[0] });
        }
    });
    const { fetchData } = useFetch<CloseRangeResDto>({
        url: "/search/kind-si-gun-gu-sportclub",
        dependancy: [() => chosenSido.value, () => chosenSigungu.value],
        param: () => ({
            clubKindCd: clubKindCd.value,
            siDoCd: chosenSido.value?.siDoCd,
            siGunGuCd: chosenSigungu.value.map(a => a.siGunGuCd),
            grEvfxCd: grEvfxCd.value,
            clubNm: clubNm.value,
            ...etcOps.value
        }),
        onSuccess: data => {
            console.log(data);
            emit("fetchList", data);
        }
    });
</script>

<template>
    <div class="searchKindClub inner-w">
        <div class="bigTabs d-flex">
            <button :class="{ 'is-active': clubKindCd === '1' }" @click="clubKindCd = '1'">전체</button>
            <button :class="{ 'is-active': clubKindCd === '2' }" @click="clubKindCd = '2'">지정스포츠클럽</button>
            <button :class="{ 'is-active': clubKindCd === '3' }" @click="clubKindCd = '3'">예비스포츠클럽</button>
        </div>
        <h3 class="title-sub mb-16">시도선택<i class="required"></i></h3>
        <ul class="tab square sidoWrap d-flex">
            <li
                class="opsBtn"
                v-for="sido in sidoArr"
                :key="sido.siDoCd"
                :class="{ 'is-active': chosenSido?.siDoCd == sido.siDoCd }"
                @click="
                    chosenSido = sido;
                    chosenSigungu = [];
                "
            >
                {{ sido.siDoNm }}&nbsp;<em class="font-roboto">({{ sido.cnt }})</em>
            </li>
        </ul>
        <h3 class="title-sub mb-16">시군구 선택<i class="required"></i></h3>
        <ul class="tab square sigunguWrap mini">
            <li
                class="opsBtn"
                v-for="sigungu in sigunguArr"
                :key="sigungu.siGunGuCd"
                :class="{ 'is-active': chosenSigungu.find(a => a.siGunGuCd === sigungu.siGunGuCd) }"
                @click="
                    () => {
                        if (sigungu.siGunGuNm === '전체') {
                            if (chosenSigungu.length && chosenSigungu.find(a => a.siGunGuNm === '전체')) {
                                chosenSigungu = [];
                            } else {
                                chosenSigungu = [{ ...sigungu }];
                            }
                            return;
                        }

                        const target = chosenSigungu.find(a => a.siGunGuCd === sigungu.siGunGuCd);
                        if (target) {
                            chosenSigungu = chosenSigungu.filter(a => a.siGunGuCd !== sigungu.siGunGuCd);
                        } else {
                            if (chosenSigungu.find(a => a.siGunGuNm === '전체')) {
                                chosenSigungu = [{ ...sigungu }];
                            } else {
                                chosenSigungu = [...chosenSigungu.map(a => ({ ...a })), { ...sigungu }];
                            }
                        }
                    }
                "
            >
                {{ sigungu.siGunGuNm }}&nbsp;<em class="font-roboto">({{ sigungu.cnt }})</em>
            </li>
        </ul>
        <div class="searchOpsBox d-flex ai-center jc-between">
            <div class="d-flex">
                <select class="jongmokSelect mr-10" v-model="grEvfxCd">
                    <option :value="op.value" v-for="op in grEvfxCdOps">{{ op.label }}</option>
                </select>
                <input class="keywordInput" v-model="clubNm" placeholder="클럽명을 입력해주세요." />
            </div>
            <div class="etcOps">
                <span class="tit mr-40">부가시설</span>

                <input type="checkbox" id="a" v-model="etcOps.pkgPsbYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="a">주차장</label>
                <input type="checkbox" id="b" v-model="etcOps.wifiOffrYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="b"> 무선인터넷 </label>
                <input type="checkbox" id="c" v-model="etcOps.shtlOprtYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="c"> 셔틀버스 </label>
                <input type="checkbox" id="d" v-model="etcOps.dsbpFaclOffrYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="d"> 장애인시설물 </label>
            </div>
            <button class="searchBtn" @click="fetchData">검색</button>
        </div>
    </div>
</template>

<style scoped lang="scss">
    .bigTabs {
        margin-bottom: 30px;

        button {
            height: 40px;
            line-height: 40px;
            padding: 0 33px;
            border-radius: 28px;
            border: solid 1px #dfe4e8;
            @include font-style(18);
            margin-right: 10px;
            color: #555;
            &.is-active {
                border: solid 1px #0fae6f;
                background-color: rgba(15, 174, 111, 0.05);
                color: #0fae6f;
                font-weight: 500;
            }
        }
    }
    .title-sub {
        @include font-style(22);
        font-weight: 500;
    }
    .tab {
        display: flex;
        flex-wrap: wrap;
        &.sidoWrap,
        &.sigunguWrap {
            margin-bottom: 60px;
        }

        li {
            @include flex;
        }

        &.square {
            /* height: 54px; */
            gap: 12px;
            li {
                border-radius: 14px;
                height: 56px;
                line-height: 56px;
                width: calc(100% / 6 - 10px);
                &.is-active {
                    color: #0fae6f;
                    border-color: #0fae6f;
                    font-weight: 500;
                    background: rgba(15, 174, 111, 0.05);
                    box-shadow: 4px 4px 18px 0 rgba(0, 0, 0, 0.15);
                }
            }

            &.mini {
                height: auto;
                gap: 9px;
                flex-wrap: wrap;
                justify-content: flex-start;

                li {
                    width: calc((100% / 10) - 8.1px);
                    height: 46px;
                    border-radius: 12px;

                    &.is-active {
                        color: #fff;
                        font-weight: 500;
                        border-color: #0fae6f;
                        background: #0fae6f;
                        box-shadow: 4px 4px 12px 0 rgba(0, 0, 0, 0.17);
                    }
                }
            }
        }
    }
    .required {
        display: inline-block;
        width: 8px;
        height: 8px;
        background: #0fae6f;
        margin-left: 13px;
        border-radius: 50%;
    }

    .font-roboto {
        font-family: $fontRoboto;
    }

    .searchOpsBox {
        %uiStyle {
            border-radius: 8px;
            border: solid 1px #d6dce5;
            background-color: #fff;
            height: 42px;
            color: #999999;
            @include font-style(15);
            padding: 0 16px;
        }
        background-color: #f9f9f9;
        border: solid 1px #e8eff4;
        border-radius: 12px;
        padding: 34px 60px;
        margin-bottom: 80px;
        select {
            @extend %uiStyle;
            cursor: pointer;
            background: url("@/assets/icon/ic_arrow.svg") no-repeat right 16px center #fff;
        }
        .keywordInput {
            @extend %uiStyle;
            width: 280px;
        }
        .etcOps {
            .tit {
                @include font-style(16);
                font-weight: 500;
            }
            .etcCheckbox {
                color: #555555;
                @include font-style(16);
                margin-right: 30px;
                cursor: pointer;
                &:last-child {
                    margin-right: 0;
                }
                &::before {
                    content: "";
                    display: inline-block;
                    width: 20px;
                    height: 20px;
                    border-radius: 4px;
                    border: solid 1px #d6dce5;
                    background-color: #fff;
                    box-sizing: border-box;
                    position: relative;
                    top: 4px;
                    margin-right: 5px;
                }
            }
            input {
                display: none;
                &:checked + label::before {
                    border: none;
                    background: url("@/assets/imgs/selectbox_active.svg") no-repeat center center #0fae6f;
                }
            }
        }
        .searchBtn {
            color: #0fae6f;
            height: 42px;
            text-align: center;
            line-height: 42px;
            border-radius: 8px;
            background-color: #fff;
            @include font-style(15);
            font-weight: 500;
            border: solid 1px #0fae6f;
            width: 90px;
        }
    }
</style>
