import ajax from "@/util/ajax";
import { MaybeRefOrGetter, ref, toValue, watch } from "vue";

type FetchParmas<T> = {
    code: string;
    enable?: MaybeRefOrGetter<boolean>;
    dependancy?: MaybeRefOrGetter[];
    isAll?: boolean;
    onSuccess?: (data: T) => void;
};

function useGetCommonOps<T>({ code, dependancy = [], enable = () => true, isAll = false, onSuccess }: FetchParmas<T>) {
    const data = ref<OpsList[]>([]);

    const fetchData = async () => {
        try {
            const res = await ajax.get(`/common/comn-code/${code}`);

            data.value = res.data.map((a: CDList) => ({ label: a.cdNm, value: a.commnCd }));
            onSuccess && onSuccess(res.data);
            isAll ? data.value.unshift({ label: "전체", value: "" }) : null;
        } catch (err) {
            console.log(err);
        } finally {
        }
    };

    watch(
        [...dependancy, enable],
        () => {
            if (toValue(enable)) {
                fetchData();
            }
        },
        { immediate: true }
    );

    return { data };
}

export default useGetCommonOps;
